export default function Menu(props) {
  const {handleMenu} = props
  return <div className="sidebar">
    <ul className="menu">
      <li><a href="#home" onClick={handleMenu}></a></li>
      <li><a href="#about" onClick={handleMenu}> </a></li>
      <li><a href="#experience" onClick={handleMenu}></a></li>
      <li><a href="#work" onClick={handleMenu}></a></li>
      <li><a href="#contact" onClick={handleMenu}></a></li>
    </ul>
  </div>
}